@import url(https://fonts.googleapis.com/css?family=Merriweather&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
.modal_delete {
  align-items: flex-start;
  background-color: #282828;
  border-radius: 5px;
  box-shadow: 1px 1px 4px 1px rgba(0,0,0,0.45);
  display: none;
  flex-direction: column;
  height: auto;
  padding: 1.4% 1% 0.4% 1%;
  position: absolute;
  width: 15%;
  z-index: 3;
  color: white;
}

.modal_delete > .material-icons {
  color: white;
  cursor: pointer;
  font-size: 1em;
  padding: 2%;
  position: absolute;
  top: 0;
  right: 0;
}

.modal_delete p {
  padding: 1%;
}

.modal_delete span {
  font-size: 1.4em;
}

.modal_delete button {
  background-color: yellowgreen;
  border-radius: 5px;
  border-style: none;
  color: white;
  cursor: pointer;
  margin: 4% auto;
  padding: 2%;
  width: 67%;
}

.modal_edit {
  background-color: #282828;
  border-radius: 5px;
  box-shadow: 1px 1px 4px 1px rgba(0,0,0,0.45);
  display: flex;
  flex-direction: column;
  height: auto;
  padding: 0.6%;
  position: absolute;
  width: 15%;
  z-index: 3;
}

.modal_edit > .material-icons {
  color: white;
  cursor: pointer;
  font-size: 1em;
  padding: 2%;
  position: absolute;
  top: 0;
  right: 0;
}

.modal_edit .input {
  color: white;
}

.modal_edit .input > .campo {
  display: flex;
  margin: 2%;
}

.modal_edit .input > .campo > input {
  margin-left: 5%;
  width: 20%;
}

.modal_edit button {
  background-color: yellowgreen;
  border-radius: 5px;
  border-style: none;
  color: white;
  cursor: pointer;
  margin: 2% 10%;
  padding: 1%;
  width: 67%;
}

