.modal_date_type {
  align-items: flex-end;
  background-color: #282828;
  border-radius: 5px;
  box-shadow: 1px 1px 4px 1px rgba(0,0,0,0.45);
  display: none;
  flex-direction: column;
  height: auto;
  padding: 0.6%;
  position: absolute;
  width: 25%;
  z-index: 3;
}

.modal_date_type > .material-icons:nth-of-type(1) {
  font-size: 1em;
  color: white;
  cursor: pointer;
}

.modal_date_type .setting {
  cursor: pointer;
  display: flex;
  justify-content: flex-end;
  margin: 2%;
}

.modal_date_type .setting > div {
  margin: 1%;
  margin-right: 5%;
  display: flex;
  align-items: center;
}

.modal_date_type .setting > div > p {
  color: white;
  font-size: 1em;
}

.modal_date_type .setting > div > .material-icons {
  box-sizing: border-box;
  font-size: 70%;
  margin-left:3%;
}

.modal_date_type .setting > div:nth-of-type(1) > .material-icons {
  color: rgb(63, 113, 252);
}

.modal_date_type .setting > div:nth-of-type(2) > .material-icons {
  color: rgb(40, 145, 55);
}

.modal_date_type .setting > div:nth-of-type(3) > .material-icons {
  color: rgb(51, 69, 98);
}

.modal_date_type .setting > div:nth-of-type(4) > .material-icons {
  color: rgb(192, 36, 98);
}

.main {
  background-color: #ecf1f8;
  display: flex;
  height: 100%;
  justify-content: space-around;
  width: 100%;
}

.main .container {
  height: 100%;
  margin: 0 2%;
  margin-right: 0;
  width: 100%;
}

.main .container2 {
  display: flex;
  flex-direction: column;
  margin: 3%;
  margin-top: 1%;
  width: 30%;
}

.comp_input {
  width: 65%;
  color: white;
  border-style: none;
  font-family: 'Anonymous Pro', monospace;
  font-weight: bold;
  border-radius: 3px;
  padding: 0.8vh;
  height: 20%;
  outline-color: transparent;
}

.servico {
  border-radius: 15px 10px 15px 15px;
  border-style: solid;
  border-color: #282828;
  border-width: 1px;
  display: flex;
  flex-direction: column;
  position: absolute;
  width: 12vw;
  z-index: 2;
}

.servico > .header {
  width: 60%;
  height: 2.5vh;
}

.servico .edit, .servico .colorize, .servico .delete {
  padding: 1px;
  position: absolute;
  top: 0.3vh;
  right: 2%;
  border-style: none;
  background-color: #5b6c80;
  color: white;
  font-size: 0.9em;
  cursor: pointer;
}

.servico .edit {
  right: 28%;
  border-radius: 10px;
}

.servico .colorize {
  right: 16%;
}

.servico .delete {
  right: 4%;
}

.servico .input-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.servico .campo {
  display: flex;
  justify-content: center;
}

.servico .campo .campo-icon {
  color: white;
  font-size: 0.8em;
  margin: 6% 5%;
  margin-right: 4%;
}

.up {
  position: absolute;
  top: 0;
  width: 100%;
  height: 5px;
  cursor: ns-resize;
}

.down {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 5px;
  cursor: ns-resize;
}

.table {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.header-table {
  height: 5%;
  display: grid;
  grid-template-columns: 9% 1fr 1fr 1fr 1fr 1fr;
}

.header-table > div span {
  color: white;
  margin: 2% 11%;
  font-size: 1em;
  font-family: 'Merriweather';
}

.header-table > div {
  margin: 0 0.5%;
  background-color: #607d8b;
  border-radius: 2px 6px 0 0;
  box-shadow: 0 1px 2px 0 rgba(0,0,0,0.7);
  display: flex;
  justify-content: flex-end;
}

.header-table button {
  border-style: none;
  background-color: #607d8b;
  cursor: pointer;
  height: auto;
  margin: 2% 4%;
}


.header-table > div .material-icons {
  color: white;
  font-size: 1em;
}

.table-date {
  display: flex;
  justify-content: center;
  margin: 1.3%;
  color: #334562;
  font-family: 'Merriweather', serif;
  font-size: 1em;
}

.table-date > h2 {
  text-align: center;
}

.body-table {
  display: flex;
  height: 100%;
  margin-bottom: 4%;
  width: 100%;
  background-color: #282828;
  border-radius: 0 0 6px 6px;
  box-shadow: 0 1px 2px 0 rgba(0,0,0,0.7);
}

.horas > p {
  flex-grow: 1;
  height: auto;
  border-width: 0.5px;
  border-style: solid;
  color: #e3e3e3;
  font-size: 0.8em;
  font-family: 'Roboto', sans-serif;
  text-align: center;
}

.horas > p:nth-of-type(13) {
  border-radius: 0 0 0 6px;
}

.horas {
  width: 10%;
  height: 100%;
  display: flex;
  flex-direction: column;
  color: #e3e3e3;
}

.board {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}

.elem {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-width: 0.5px;
  border-color: #e3e3e3;
  border-style: solid;
}

.elem:nth-of-type(5) {
  border-radius: 0 0 6px 0;
}

.elem > .cell {
  height: 100%;
  width: 100%;
  border-width: 0.5px;
  border-color: #dadce0;
  border-top-style: solid;
}

.elem > div:nth-of-type(1) {
  border-top-style: none;
}

.calendar {
  background-color: #282828;
  border-radius: 4px 12px 4px 4px;
  box-shadow: 1px 1px 10px 1px rgba(0,0,0,0.75);
  margin: 0 2%;
}

.calendar-header {
  align-items: center;
  background-color: #cddc39;
  border-radius: 4px 12px 2px 2px;
  display: flex;
  font-size: 1.4em;
  height: 22%;
  width: 100%;
}

.calendar-header > i {
  color: white;
  cursor: pointer;
  font-size: 1.5em;
  margin: 5%;
}

.header-data {
  align-items: center;
  color: white;
  display: flex;
  font-family: 'Roboto';
  font-size: 1em;
  font-weight: bold;
  justify-content: center;
  height: 100%;
  width: 100%;
}

.header-data > .calendar-year {
  margin-left: 5%;
}

.calendar-body {
  height: auto;
  margin: 6% 0;
  width: auto;
}

.weeks {
  align-items: center;
  display: flex;
  justify-content: space-around;
  margin: 6%;
  margin-bottom: 3%;
}

.weeks > span {
  color: white;
  font-family: 'Roboto';
  font-size: 0.6em;
  font-weight: bold;
  margin: 0.7%;
  padding: 2%;
}

.days {
  display: grid;
  grid-template-columns: repeat(7 , 1fr);
  grid-template-rows: repeat(5 , 1fr);
  margin: 0 6%;
}

.days > span {
  font-family: 'Roboto';
  font-size: 0.6em;
  color: white;
  cursor: pointer;
  text-align: center;
  margin: 5% 1%;
  padding: 15%;
}

.days > span:hover {
  background-color: #d3d3d3;
}

.fileArea {
  width: 100%;
  display: flex;
  justify-content: center;
  
}

.dropArea {
  background-color: #f1f1f1;
  margin: 8% 0;
  width: 100%;
  height: 70%;
  border-radius: 10px;
  border-style: dashed;
  border-width: 1.5px;
  border-color: #4b5972;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.dropArea div {
  display: flex;
  justify-items: center;
  align-items: center;
  flex-direction: column;
}

.dropArea div i {
  font-size: 1.3em;
  color: #334562;
}

.dropArea div span {
  font-size: 1.2em;
  text-align: center;
  color: #334562;
}

.btn-save {
  background-color: #b48967;
  border-style: none;
  border-width: 0.5px;
  box-shadow: 0 1px 5px 0 rgba(0,0,0,0.7);
  color: white;
  cursor: pointer;
  margin-top: 5%;
  padding: 4%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn-save > span {
  font-family: 'Merriweather', serif;
  font-size: 1.2em;
  padding: 3% 7%;
  padding-top: 4.8%;
}

.btn-save > .material-icons {
  font-size: 1.5em;;
}

.btn-save:hover {
  background-color: #885c3b;
}

.notifications {
  display: flex;
  justify-content: flex-end;
  margin-top: 1%;
  margin-bottom: 8%;
  width: 100%;
}

.notifications .icon {
  color: #282828;
  font-size: 2.5em;
  cursor: pointer;
}

.notifications .red {
  background-color: #c91b0e;
  border-radius: 10px;
  height: 10px;
  right: 3.7vw;
  position: absolute;
  top: 6.6vh;
  width: 10px;
}

.notifications .dropdown {
  background-color: aliceblue;
  box-shadow: 1px 1px 10px 1px rgba(0,0,0,0.45);
  height: 30vh;
  top: 9vh;
  position: absolute;
  width: 21vw;
}

.notifications .dropdown .item {
  background-color: blueviolet;
  color: white;
  padding: 2%;
  text-align: center;
  width: 100%;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body, .App, #root {
  height: 100%;
  width: 100%;
}

